.gameContainer {
    .gameInformationContainer {
        background-color: rgba(0, 0, 0, 0.2);
        width: calc(50em - 1em);
        min-height: calc(30em - 1em);
        border-radius: .1em;
        margin: auto;
        margin-top: 5em;
        padding: 1em;
        .quizInformation {
            color: $color5;
            margin: 1em auto;
            width: 80%;
            height: 6.5em;
            border-radius: .1em;
            font-size: 1.5em;
            .quizName {
                text-align: center;
                font-size: 2em;
                margin: 0;
                line-height: 1em;
            }
            .quizQuestions {
                text-align: center;
                line-height: 1em;
            }
            .quizHost {
                text-align: center;
                line-height: 1em;
            }
        }
        .playersContainer {
            background-color: rgba(255, 252, 249, .1);
            color: $color5;
            margin: 1em auto;
            width: 80%;
            height: 10em;
            border-radius: .1em;
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            flex-direction: column;
            .player {
                height: 2em;
                line-height: 2em;
                padding: 0 .2em;
                width: calc(25% - .4em);
            }
        }
        .buttonContainer {
            display: flex;
            width: 80%;
            margin: 1em auto;
            .quizButton {
                border-radius: .1em;
                border: none;
                flex-grow: 1;
                background-color: $color2;
                color: $color5;
                height: 2.5em;
                font-size: 1.2em;
                font-weight: bold;
                text-align: center;
                line-height: 2.3em;
            }
            .startButton {
                margin-right: 1em;
                background-color: $color4;
                color: $color5;
            }
            .leaveButton {
                margin-left: 1em;
            }
            .quizButton:hover {
                cursor: pointer;
                text-decoration: none;
                box-shadow: inset 0 0 500px rgba(0, 0, 0, 0.2);
            }
        }
    }
    .countdownContainer {
        .countdownTimer {
            width: 100;
            height: 100%;
            color: $color5;
            text-align: center;
            font-size: 25em;
            line-height: 100%;
            animation: changeFontSize .999999s ease-out 5;
        }
        @keyframes changeFontSize {
            0% {
                font-size: 10em;
            }
            100% {
                font-size: 25em;
            }
        }
    }
    .activeGameContainer {
        .questionMainContainer {
            color: $color5;
            text-align: center;
            .questionContainer {
                .question {
                    font-size: 2em;
                }
                .questionCountdown {
                    padding: 0;
                    margin: 0;
                    font-size: 5em;
                }
            }
            .answersContainer {
                background-color: rgba(255, 252, 249, 0.1);
                color: $color5;
                margin: auto;
                width: 90%;
                min-height: 8em;
                border-radius: .1em;
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                padding: 1em;
                .answer {
                    width: calc(50% - .4em);
                    margin: .2em .2em;
                    flex-grow: 2;
                    height: 2em;
                    background-color: $color4;
                    border: none;
                    font-size: 2em;
                    color: $color5;
                }
                .answer:hover {
                    cursor: pointer;
                    text-decoration: none;
                    background-color: $color2;
                }
                .answerWaiting {
                    font-size: 3em;
                    width: 100%;
                    text-align: center;
                    line-height: 2em;
                }
                /**
                * @author: Martin Wolf
                * Retrieved at the 10.11.2018
                * Animation is taken from: 
                * Source: https://martinwolf.org/before-2018/blog/2015/01/pure-css-savingloading-dots-animation/
                */
                @keyframes blink {
                    0% {
                        opacity: .2;
                    }
                    20% {
                        opacity: 1;
                    }
                    100% {
                        opacity: .2;
                    }
                }
                .answerWaiting span {
                    animation-name: blink;
                    animation-duration: 1.4s;
                    animation-iteration-count: infinite;
                    animation-fill-mode: both;
                }
                .answerWaiting span:nth-child(2) {
                    animation-delay: .2s;
                }
                .answerWaiting span:nth-child(3) {
                    animation-delay: .4s;
                }
            }
            .nextQuestionButton {
                border: none;
                margin: 0 auto;
                border-radius: .1em;
                font-size: 2em;
                background-color: $color4;
                width: calc(75% - .4em);
                height: 5em;
                background-color: $color4;
                font-size: 2em;
                color: $color5;
            }
            .nextQuestionButton:hover {
                cursor: pointer;
                text-decoration: none;
                background-color: $color2;
            }
            .resultContainer {
                width: 100%;
                background-color: $color3;
                table {
                    width: 100%;

                    tbody {
                        width: 100%;
                        
                    }

                    th {
                        border-bottom: 1px solid black;
                        color: #000;
                    }
                    td {
                        width: 33.3%;
                        border-bottom: 1px solid black;
                        color: #000;
                    }
                }
            }

            h2 {
                color: black;
            }
            .winnerContainer{
                color: black; 
                font-size: 1.5em;
            }
            .returnHomeButton {
                background-color: $color4;
                margin: 0 auto;
                margin-top: 1em; 
                padding:1em;
            }

            .returnHomeButton:hover{
                cursor: pointer;
                text-decoration: none;
                box-shadow: inset 0 0 500px rgba(0, 0, 0, 0.2);
            }
        }
    }
}